.location {
  color: gray;
  text-decoration: none;
}

a {
  text-decoration: none;
}

.MessageContainer {
  color: black;
}

.MessageContainer__message--left {
  padding: 0.15rem;
  background: lightgrey;
  margin: 0.5rem 0rem;
  max-width: 20%;
}

.MessageContainer__message--right {
  color: white;
  padding: 0.15rem;
  margin: 0.5rem 0rem;
  background: slateblue;
  max-width: 20%;
  transform: translateX(350%);
}

.profile {
  width: 85%;
}

.profile header h3 {
  margin: 2rem 0rem;
  color: grey;
}

.profile__imagecontainer {
  margin: 1rem 0rem;
  display: flex;
  justify-content: center;
}

.profile__imagecontainer img {
  overflow: hidden;
  width: 20rem;
  height: 20rem;
  background-color: grey;
  border-radius: 50%;
}

.profileForm {
  display: flex;
  justify-content: center;
}

.profileForm__container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  max-width: 90rem;
}

.profileForm input {
  background-color: #e4e9f7;
  font-size: 1.6rem;
}

.profileForm .MuiInputLabel-formControl {
  font-size: 1.8rem;
}

#pricingPlanSection {
  padding-top: 10rem;
  padding-bottom: 10rem;
}

#pricingPlanSection main {
  text-align: center;
}

#pricingPlanSection main h2 {
  color: #523cdc;
  font-size: 1.6rem;
}

#pricingPlanSection main h1 {
  color: black;
  font-size: 3.7rem;
}

#pricingPlanSection main .container {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media only screen and (max-width: 1187px) {
  #pricingPlanSection main .container {
    flex-wrap: wrap;
  }
}

#pricingPlanSection main .priceCard {
  border-radius: 0.5rem;
  padding: 3rem 6rem;
  width: 36rem;
  width: 34rem;
  background: #f9f9f9;
  margin: 0;
}

@media only screen and (max-width: 788px) {
  #pricingPlanSection main .priceCard {
    width: 80%;
  }
}

#pricingPlanSection main .priceCard h1 {
  margin-top: 1rem;
  font-size: 3.5rem;
}

#pricingPlanSection main .priceCard h2 {
  margin-top: 2rem;
  font-size: 1.8rem;
}

#pricingPlanSection main .priceCard p {
  line-height: 2.5rem;
  margin-top: 4rem;
  color: #6a7c92;
  font-size: 1.8rem;
}

#pricingPlanSection main .priceCard button {
  transition: all 200ms ease-in-out;
  border-radius: 3rem;
  background: transparent;
  font-size: 1.6rem;
  margin-top: 3rem;
  border: 1px solid #523cdc;
  color: #523cdc;
  width: 15rem;
  height: 5rem;
}

#pricingPlanSection main .priceCard button:hover {
  transition: all 100ms ease-in-out;
  color: white;
  background: #523cdc;
}

#pricingPlanSection main .centerPricingCard {
  background-image: linear-gradient(0deg, #301cab 0%, #4448d3 52%, #5774fa 100%);
  padding: 9rem 2rem !important;
}

#pricingPlanSection main .centerPricingCard h1 {
  color: white;
}

#pricingPlanSection main .centerPricingCard h2 {
  color: white;
}

#pricingPlanSection main .centerPricingCard p {
  color: white;
}

#pricingPlanSection main .centerPricingCard button {
  transition: all 200ms ease-in-out;
  border-radius: 3rem;
  background: transparent;
  font-size: 1.6rem;
  margin-top: 3rem;
  border: 1px solid white;
  color: white;
  width: 15rem;
  height: 5rem;
}

#pricingPlanSection main .centerPricingCard button:hover {
  transition: all 100ms ease-in-out;
  color: #523cdc;
  background: white;
}

